import React from 'react'
import { Link, graphql } from 'gatsby'

import Layout from '../components/layout'

import {
  Window,
  TitleBar,
  Button,
  ButtonRed,
  ButtonYellow,
  ButtonGreen,
  Content,
  Title,
} from '../styling/page.style'

const About = ({ data }) => {
  return data.allPagesJson.edges.map(({ node, index }) => {
    return (
      <Layout>
        <h2>{node.title}</h2>
        <p>{node.intro}</p>
        <Window>
          <TitleBar>
            <Button>
              <ButtonRed />
              <ButtonYellow />
              <ButtonGreen />
            </Button>
          </TitleBar>
          <Content>
            <div key={index}>
              <Title>/* SUMMARY OF QUALIFICATIONS */</Title>
              <div>
                Received a Bachelors degree in the Information Technology at the
                Universitas Teknologi Yogyakarta.
              </div>
              <br />
              <div>
                Excellent organization, self-motivation, and problem-solving
                skills.
              </div>
              <br />
              <div>
                Creative, strong communication skills and consistently meets
                deadlines.
              </div>
              <br />
              <div>Passionate about web development and new technologies.</div>
              <Title>/* --Technology-- */</Title>
              {node.skill.map((item, index) => {
                return <div key={`${index}${item}`}>- {item}</div>
              })}
              <Title>/* --Favorite Editor-- */</Title>
              <div>{node.favorite_editor}</div>
            </div>
          </Content>
        </Window>
      </Layout>
    )
  })
}

export default About

export const query = graphql`
  query {
    allPagesJson(filter: { path: { eq: "about" } }) {
      edges {
        node {
          path
          title
          intro
          skill
          favorite_editor
        }
      }
    }
  }
`
